/* ==================================================================
## Config
===================================================================== */
	
$sml-font-size:			$bd-sml-font-size;
$big-font-size:			$bd-big-font-size;
$big-font-size-tiny: 	$body-font-size;
$big-font-size-mobile: 	$bd-big-font-size-minus;
$big-font-size-narrow: 	$bd-big-font-size;
$big-font-size-wide: 	$bd-big-font-size;
$big-font-size-wider: 	$bd-big-font-size;
$big-font-size-widest: 	$bd-big-font-size-plus;
$big-font-size-full: 	$bd-big-font-size-plus;

/* ==================================================================
## Def 
===================================================================== */

.bd {

	text-align:			justify;

	> p {

		font-size:		inherit;
	}
}

.bd--bigtext > p {

	font-size:			$big-font-size;
	line-height:		1.6;
	margin: 			$base-spacing;

	@include tiny 	{ 	font-size: $big-font-size-tiny; 	}
	@include mobile { 	font-size: $big-font-size-mobile; 	}
	@include narrow { 	font-size: $big-font-size-narrow; 	}
	@include wide 	{ 	font-size: $big-font-size-wide; 	}
	@include wider 	{ 	font-size: $big-font-size-wider; 	}
	@include widest { 	font-size: $big-font-size-widest; color: white; }
	@include full 	{ 	font-size: $big-font-size-full; color: white; }
}

.bd--smltext > p {

	font-size: 			$sml-font-size;
	line-height: 		1.25rem;
}